.page--calendar #main .type-page.entry,
#theme-nav,
body.theme--esters {
	background-color: #f7f7ef;
	color: #bf9d3e;
}
.xdebug-error tr:first-child th {
	color: #007af5;
}

::-webkit-search-cancel-button,
::-webkit-input-placeholder,
.woocommerce-page .entry-content mark,
.woocommerce-page .entry-content ins,
.menu-tabs .tabs__title,
.page--our-menu #content .menu-tabs h3,
.page--our-menu #content .menu-tabs h4,
#home-slideshow a:hover,
#theme-nav a:hover,
#masthead a:hover,
.woocommerce-account #page .woocommerce-MyAccount-navigation a:hover,
.site a {
	color: #a08068;
}
.site a:not(.btn):hover {
	color: #a16f68;
}
.theme--esters #main .products a:not(.btn) {
	color: inherit;
}

.menu-tabs .tabs__title:hover,
.menu-tabs .tabs__title.selected {
	color: #179F9A;
}

#colophon .wp-block-button__link,
#content .entry-content .wp-block-button__link,
#content .entry-content .btn {
	color: #bf9d3e;
	background-color: white;
}
#theme-header__cart__num,
#colophon .wp-block-button__link:hover,
#content .entry-content .wp-block-button__link:hover,
#content .entry-content .btn:hover,
#press-articles .entry main a::before,
.page--our-menu #content .entry-content > p,
.page--shop-landing #content .entry-content .btn,
.page--shop-landing #content .entry-content .wp-block-button__link,
.site input[type="submit"],
.site input[type="button"],
.site button,
#content .woocommerce .button,
#content .entry-content .woocommerce .btn,
.site .btn {
	color: white;
	background-color: #bf9d3e;
}
#content .woocommerce .button:hover,
.page--shop-landing #content .entry-content .btn:hover,
.page--shop-landing #content .entry-content .wp-block-button__link:hover,
.site input[type="submit"]:hover,
.site input[type="button"]:hover,
.site button:hover,
.site .btn:hover {
	color: white;
	background-color: #a08068;
}
#home-slideshow a,
#theme-nav a,
#masthead a {
	color: inherit;
	text-decoration: none;
}
#theme-header__online-shops::before,
#theme-header__online-shops::after {
	border-color: #a08068;
}
#theme-header__online-shops li::before {
	border-left: 3px dotted #a08068;
}
#theme-header__cart svg {
	color: black;
}
.page-template #main {
	border-bottom: 1px solid #bf9d3e;
}

#order-online-modal h1,
#order-online-modal nav a,
.modal .modal__close-btn,
#home-slideshow h2,
#home-slideshow h3 {
	color: white;
}

.page--shop-landing .wp-block-column {
	background-color: #f6f4eb;
}

.single-product #main > .product .single_variation_wrap .woocommerce-Price-amount {
	color: #77a464;
}
